body {
  background-color: whitesmoke;
}

.break {
  text-align: center;
  font: bold;
}

/* a:link {
  color: #34baef;
  text-decoration: none;
} */

/* CONTENT START */
/* ============= */
.App {
  text-align: center;
  margin: 30px 50px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

/* HEADER */
.App-header {
  border-bottom: 2px solid black;
  /* background-color: #34baef; */
  background: -webkit-gradient(linear, left top, right top, from(#34baef), to(#c9f0ff));
  background: -webkit-linear-gradient(left, #34baef, #c9f0ff);
  background: -o-linear-gradient(left, #34baef, #c9f0ff);
  background: linear-gradient(to right, #34baef, #c9f0ff);
  height: 75px;
  padding: 20px;
  color: #ffff;
}

.App-title {
  font-size: 1.5em;
  font-family: Helvetica, Arial, sans-serif;
}

.app-header {
  text-align: right;
}

/* NAVIGATION */
.nav {
  text-align: center;
  margin: 5px 5px;
}

.nav ol {
  list-style: none;
  font-size: 1em;
}

.nav li {
  display: inline;
}

.nav a {
  padding: 10px;
  margin: 15px 10px;
  text-decoration: none;
  color: #071013;
  opacity: inherit;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.nav a:hover {
  cursor: pointer;
  border-radius: 20px;
  color: #ffff;
  background-color: #34baef;
  opacity: 1;
}

.code {
  font-style: bold;
  font-size: 16px;
  border: 1px solid black;
  padding: 0.5em;
  background-color: rgba(0, 0, 0, 0.1);
}

/* DISPLAY */
.display {
  font-size: medium;
  font-family: Helvetica, Arial, sans-serif;
  border-top: 2px solid #071013;
  border-bottom: 2px solid #071013;
  background-color: #ffff;
}

.display h2 {
  font-size: 40px;
  color: #071013;
  /* background-color: #c9f0ff; */
  background: -webkit-gradient(linear, left top, right bottom, from(#c9f0ff), to(#34baef));
  background: -webkit-linear-gradient(top left, #c9f0ff, #34baef);
  background: -o-linear-gradient(top left, #c9f0ff, #34baef);
  background: linear-gradient(to bottom right, #c9f0ff, #34baef);
  border: 1px solid #071013;
  padding: 10px 5px;
  /* height: 80px; */
  text-align: center;
}

.display h3 {
  padding-top: 20px;
  font-size: 30px;
}

/* Display Blocks */
.blocks {
  text-align: left;
  color: #071013;
  padding: 20px 175px;
  /* margin: 50px 50px; */
}

.blocks p {
  font-family: Helvetica, Arial, sans-serif;
}

/* FOOTER */

.foot-block {
  margin: 5px 5px;
}

.foot-block ol {
  list-style: none;
  font-size: 1em;
}

.foot-block li {
  display: inline;
  padding: 10px 10px;
  margin: 15px 15px;
  text-decoration: none;
  opacity: inherit;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.foot-block li:hover {
  cursor: pointer;
  border-radius: 20px;
  color: #ffff;
  background-color: #34baef;
  opacity: 1;
}

.foot-block li:visited {
  text-decoration: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* BLOG */

.blog {
  margin: 10px 0px;
  padding: 2px;
  border-bottom: 2px solid #071013;
}

.blog h3 {
  padding: 8px;
  border-bottom: 2px solid #071013;
  text-align: left;
  font-size: 2em;
  font-style: italic;
  color: #ffff;
  /* background-color: #34baef; */
  background: -webkit-gradient(linear, left top, right top, from(#34baef), to(#c9f0ff));
  background: -webkit-linear-gradient(left, #34baef, #c9f0ff);
  background: -o-linear-gradient(left, #34baef, #c9f0ff);
  background: linear-gradient(to right, #34baef, #c9f0ff);
  border-radius: 15px;
}

.bloglink {
  /* border: 2px solid #071013; */
  border-radius: 5px;
  display: block;
  padding: 15px;
  margin: 10px 0px;
  text-align: left;
  text-decoration: none;
  text-shadow: 4px 4px 5px #c9f0ff;
  font-size: 1.5em;
  font-family: Helvetica, sans-serif;
  color: #071013;
  background: -webkit-gradient(linear, left top, left bottom, from(#c9f0ff), to(#34baef));
  background: -webkit-linear-gradient(#c9f0ff, #34baef);
  background: -o-linear-gradient(#c9f0ff, #34baef);
  background: linear-gradient(#c9f0ff, #34baef);
}



